@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin content-padding-inline {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 72px;
  }

  @include on-desktop {
    padding-inline: 120px;
    margin-inline: auto;
    max-width: 1260px;
  }
}

.container {
  @include content-padding-inline;
}

@mixin page-grid() {
  --columns: 2;

display: grid;
column-gap: 20px;
grid-template-columns: repeat(var(--columns), 1fr);

@include on-tablet {
  --columns: 6;
  
  column-gap: 24px;
}

@include on-desktop {
  --columns: 12;
}
}

@mixin mouse-hover {
  cursor: url(../img/Cursor-Mouse-Mice-Hover.svg), auto;
}
